* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: $text;
}

body {
    color: $text;
    background-color: $background;
    font-family: 'ProximaNova';


}

.container {

    width: 100%;
    max-width: 1164px;
    padding: 0 12px;

}

.btn {
    &.btn-primary {
        background: $primary-color;
        font-size: 15px;
        border-color: $primary-color;
        padding: 13px 28px;
        border: none;
        border-radius: 90px;
        font-weight: 600;
        font-style: normal;
        font-weight: 600;
        display: flex;
        justify-content: space-between;
        line-height: 146%;

        &:focus {
            box-shadow: none;
        }

        &:hover {
            background-color: $secondary-color;
            border: $secondary-color;
        }

        &.btn-large {
            padding: 22px 25px;
            font-weight: 600;
            font-size: 16px;
            line-height: 146%;

        }
    }


}

.container-fluid {
    padding: 0 50px;
    max-width: 1920px;
}

.template-title,
.template-subtitle{
    line-height: 124.5%;
    text-align: center;
}

.template-title{
    position: relative;
    margin-bottom: $title-margin + 19px;

 h2{
    font-weight: 700;
    font-size: 36px;
    text-align: center;
    text-transform: uppercase;
 }
 h3{
    font-size: 24px;
    font-weight: 300;
 }

 &:after{
    content: "";
    width: 85px;
    height: 2px;
    left: 50%;
    bottom: -19px;
    transform: translate(-50%,0);
    position: absolute;
    background-color: $primary-color;
}

}
.template-title-left{
    
    @extend .template-title;
    margin-bottom: $title-margin;
    h2{
        text-align: left;
    }
    &:after{
     left: 0;
     transform: translate(0,0);
    }

}
.before-inline{
    > &:after{
 display: none;
    }
    h2{
        &:after{
            content: "";
            width: 85px;
            height: 2px;
            background-color: $primary-color;
            margin: 0 0 10px 20px;
            display: inline-block;
        }
    }
}

.input-box{
    margin-bottom: 20px;
}
.input-checkbox{
    
    margin-bottom: 20px;
    display: flex;
    justify-content: baseline;

    
    label{
        margin: 3px 0 0 9px;
       
    }
    input{
        
        width: 24px;
        height: 24px;
        
        
    }
}

label{
    font-weight: 600;
    font-size: 15px;
    line-height: 126%;
    margin-bottom: 6px;
}
input{
    width: 100%;
    background: white;
    border-radius: 10px;
    padding: 19px 20px;
    font-size: 15px;
    line-height: 126%;
    transition: .3s;
    color: black;
    border: 1px solid rgb(255, 251, 251);
    &::placeholder{
        color: #CBCBCB;
        transition: .3s;
    }
    &:focus{
        border: 1px solid rgb(138, 132, 132);
        box-shadow: rgba(211, 211, 232, 0.818) 0px 0px 11px 0px;
        &::placeholder{
            color: transparent;
        }
    }
    &:focus-visible{
        outline: none;
    }

    
}