header {

    padding: 25px 0 39px;
    
    .nav-link {
        color: $text;
        font-weight: 400;
        font-size: 16px;

        &:hover {
            color: $secondary-color;
        }
    }

    .navbar-nav {
        margin: 0 auto;
    }

    .navbar {
        padding: 0;
    }

    .header-logo {
        display: inline-block;

    }

    .navbar-toggler {

        width: 40px;
        height: 40px;
        position: relative;

        &:focus {
            box-shadow: none;
        }

        span,
        span:before,
        span:after {
            cursor: pointer;
            border-radius: 1px;
            height: 2px;
            width: 35px;
            background: white;
            position: absolute;
            left: 0;
            top: 50%;
            display: block;
            content: '';
        }

        span:before {
            top: -10px;
        }

        span:after {
            top: 10px;
        }

        span,
        span:before,
        span:after {
            transition: all 0.5s ease-in-out;
        }
        &[aria-expanded="true"] {
            span {
                background-color: transparent;
            }
            span:before,
            span:after {
                top: 0;
            }
            span:before {
                transform: rotate(135deg);
            }
            span:after {
                transform: rotate(-135deg);
            }
          }
    }


    

}
