.about-company{
  @extend .container;
  padding:$section-padding;
  display: flex;
  margin: auto;
  
  &__text{
    margin-right: 25px;
}

&__subtitle{
    font-weight: 300;
    font-size: 18px;
    line-height: 170%;
}
.container{
    display: flex;
    margin-top: 91px;
    margin-bottom: 50px;

}

}
